import React from 'react';

const Resume = () => (
    <div className="container resume"> 
    <div className="row">
    <h1>Skills</h1>
    <ul>
        <h3>Front End Skills:</h3>
        <li>HTML</li>
        <li>CSS</li>
        <li>Jquery</li>
        <li>React</li>
        <li>Bootstrap</li>
    </ul>
    <ul>
        <h3>Back End Skill:</h3>
        <li>Node.js</li>
        <li>API</li>
        <li>Express</li>
        <li>MySQL</li>
        <li>MongoDB</li>
    </ul>

    </div>
    </div>


);

export default Resume;